<template>
    <v-container grid-list-lg id='parcelCreation' align='center' style='margin-bottom:15px;'>
        
        <v-layout row wrap>
    
            <v-flex xs12 sm12 md12>
                <!-- Parcel Start -->
                <v-card v-show='parcelIntro' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                            <v-tabs 
                            center-active
                            color='white'
                            v-model='selectedCategory'
                            class="mx-2"
                            active-class='primary lighten-1'
                            icon>
                            <v-tab class='secondary--text'>България</v-tab>
                            <v-tab class='secondary--text lighten-2'>Международни</v-tab>
                            <v-tab disabled class='secondary--text lighten-2'>Палетни</v-tab>
                            <v-tab disabled class='secondary--text lighten-2'>Експресни</v-tab>
                            </v-tabs>
                        </v-app-bar>
                    </v-card-title>

                    <v-form v-show='selectedCategory == 0'
                        ref="form_intro"
                        @submit.prevent=""
                        @submit="checkParcelPrice"
                        v-model="valid_form_intro"
                        lazy-validation
                    >
                    <v-layout row wrap justify-center>
                        <v-flex xs10 sm10 md4>
                            <v-select
                            required
                            :rules='parcelValidationServiceRules'
                            :disabled='canProceedOrder'
                            v-model="serviceType"
                            :items="typeOfService"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Избери вида на доставката"
                            ></v-select>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-select
                            required
                            :rules='parcelValidationParcelRules'
                            v-model="parcelType"
                            background-color='white darken-3'
                            filled
                            :items="typeOfParcel"
                            :menu-props="{ maxHeight: '200' }"
                            label="Избери вида на пратката"
                            @change="documentPackage"
                            ></v-select>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-text-field
                            required
                            :rules="parcelValidationWeightRules"
                            suffix='kg'
                            v-model="weight"
                            background-color='white darken-3'
                            filled
                            :disabled='weightEnabled'
                            label="Тежест на пратката (в кг.)" 
                            hint="Въведете по-високата стойност от: Tегло или обемно тегло"
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout v-if='officeService' row wrap justify-center>
                        <v-flex xs10 sm10 md4>
                            <v-autocomplete
                            :loading='officesLoading'
                            :disabled='officesLoading'
                            required
                            :rules='parcelValidationAddressRules'
                            v-model="senderOffice"
                            :items="evropatOffices"
                            clearable
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис изпращане"
                            persistent-hint
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md4>
                            <v-autocomplete
                            :loading='officesLoading'
                            :disabled='officesLoading'
                            required
                            :rules='parcelValidationAddressRules'
                            v-model="recipientOffice"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatOffices"
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис получаване"
                            persistent-hint
                            ></v-autocomplete>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='officeAddressService' row wrap justify-center>
                        <v-flex xs10 sm10 md3>
                            <v-autocomplete
                            :loading='officesLoading'
                            :disabled='officesLoading'
                            v-model="senderOffice"
                            :items="evropatOffices"
                            background-color='white darken-3'
                            clearable
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис изпращане"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="recipientProvince"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinces"
                            :menu-props="{ maxHeight: '200' }"
                            label="Област на получател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md4>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="recipientCity"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinceCitiesRecipient"
                            :menu-props="{ maxHeight: '200' }"
                            label="Населено място на получател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='addressOfficeService' row wrap justify-center>
                        <v-flex xs10 sm10 md3>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="senderProvince"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinces"
                            :menu-props="{ maxHeight: '200' }"
                            label="Област на подател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md4>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="senderCity"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinceCitiesSender"
                            :menu-props="{ maxHeight: '200' }"
                            label="Населено място на подател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-autocomplete
                            :loading='officesLoading'
                            :disabled='officesLoading'
                            v-model="recipientOffice"
                            clearable
                            background-color='white darken-3'
                            filled
                            :items="evropatOffices"
                            :menu-props="{ maxHeight: '200' }"
                            label="Офис получаване"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                    </v-layout>

                    <v-layout v-else-if='addressService' row wrap justify-center>
                        <v-flex xs10 sm10 md2>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="senderProvince"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinces"
                            :menu-props="{ maxHeight: '200' }"
                            label="Област на подател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="senderCity"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinceCitiesSender"
                            :menu-props="{ maxHeight: '200' }"
                            label="Населено място на подател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md2>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="recipientProvince"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinces"
                            :menu-props="{ maxHeight: '200' }"
                            label="Област на получател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="recipientCity"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinceCitiesRecipient"
                            :menu-props="{ maxHeight: '200' }"
                            label="Населено място на получател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                    </v-layout>

                    <v-card-text>
                        Вижте партньорскара ни офисна мрежа <a target="_blank" href='https://evropat.bg/offices/'>тук</a>!
                    </v-card-text>

                    <!-- <v-layout row wrap align-center justify-center>
                        <v-flex xs9 sm9 md4>
                            <v-checkbox
                            color='primary'
                            v-model="is_parcel_outside_schedule"
                            style='black--text'
                            label="Искам пратката да ми се изпълни възможно най-скоро, извън график."
                            hide-details>
                            </v-checkbox>
                        </v-flex>
                    </v-layout> -->

                    <v-layout row wrap align-center justify-center>
                        <v-flex xs8 sm4 md3>
                            <v-btn
                            block
                            rounded
                            color="primary"
                            type="submit"
                            :disabled='canProceedOrder'
                        >
                            Покажи цена</v-btn
                        >
                        </v-flex>
                        <v-flex v-if='parcelPriceRecieved' justify-center xs8 sm6 md2>
                            <v-text-field
                            readonly
                            :value='parcelPrice'
                            dense
                            suffix='лв.'
                            class="text-md-h5 font-weight-bold centered-input mt-3"
                            color="primary" >
                            </v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-expand-x-transition ease-in>
                     <v-layout v-show='is_parcel_outside_schedule' row wrap justify-center>
                        <v-flex xs8 sm10 md10>
                                <v-alert
                                type="info"
                                dismissible
                                > {{ scheduled_day_delivery }}
                                </v-alert>
                        </v-flex>
                    </v-layout>
                    </v-expand-x-transition>

                    <v-expand-x-transition ease-in>
                     <v-layout v-if='parcelAlert' row wrap justify-center>
                        <v-flex xs8 sm10 md10>
                                <v-alert
                                type="error"
                                dismissible
                                > {{ parcelMessage }}
                                </v-alert>
                        </v-flex>
                    </v-layout>
                    </v-expand-x-transition>

                    <v-card-text color='primary'>
                        Цените са крайни, с вкл. такса за гориво, тол и включено ДДС.<br>
                        <strong>Калкулира се обемно тегло на пратката в случай че надвишава реалното по формула Д*Ш*В/6000</strong><br>
                        Пример: кашон с размери 50х50х50см - обемно тегло 50*50*50=125,000 / 6000 = 20,83кг <br>
                    </v-card-text>

                    <v-layout v-if='parcelPriceRecieved' row wrap justify-center>
                        <v-flex xs8 sm10 md3>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            :disabled='canProceedOrder'
                            @click="proceedDetails">
                                Напред
                            </v-btn>
                        </v-flex>
                    </v-layout>
                    </v-form>

                    <v-form v-show='selectedCategory == 1'
                        ref="international_form_intro"
                        @submit.prevent=""
                        @submit="checkParcelInternationalPrice"
                        v-model="international_valid_form_intro"
                        lazy-validation
                    >
                    <!-- Parcel Main Data -->
                    <v-layout row wrap justify-center>
                        <v-flex xs10 sm10 md7>
                            <v-select
                            required
                            :rules='parcelValidationServiceRules'
                            :disabled='canProceedOrder'
                            v-model="serviceType_internaltional"
                            :items="typeOfService_internaltional"
                            item-value="value"
                            item-text="description"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Избери вида на услугата"
                            class="primary--hint"
                            :hint="serviceType_internaltional_description"
                            ></v-select>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-text-field
                            required
                            :rules="international_parcelValidationWeightRules"
                            suffix='kg'
                            v-model="international_weight"
                            background-color='white darken-3'
                            filled
                            label="Тежест на пратката (в кг.)" 
                            :hint="internationalWeightRestrict"
                            persistent-hint
                            @forminput="international_weightValidate"
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <!-- Destinations -->
                    <v-layout row wrap justify-center>
                        <v-flex xs10 sm10 md3>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="senderProvince"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinces"
                            :menu-props="{ maxHeight: '200' }"
                            label="Област на подател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <v-flex xs10 sm10 md4>
                            <v-autocomplete
                            :loading='destinationsLoading'
                            :disabled='destinationsLoading'
                            v-model="senderCity"
                            background-color='white darken-3'
                            clearable
                            filled
                            :items="evropatProvinceCitiesSender"
                            :menu-props="{ maxHeight: '200' }"
                            label="Населено място на подател"
                            persistent-hint
                            required
                            :rules='parcelValidationAddressRules'
                            ></v-autocomplete>
                        </v-flex>
                        <!-- v-model="parcelType"
                            :items="typeOfParcel" -->
                        <v-flex xs10 sm10 md3>
                            <v-select
                            required
                            :rules='parcelValidationParcelRules'
                            :items="international_countries_list"
                            v-model="recipientCountry"
                            item-value="code"
                            item-text="name"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Държава на получателя"
                            ></v-select>
                            <!-- <country-select className='v-select' v-model="country_international" :country="country_international"/> -->
                        </v-flex>
                    </v-layout>

                    <!-- Extra -->
                    <v-layout row wrap justify-center>
                        <v-flex xs10 sm10 md5>
                            <v-select
                            v-model="insurance_international"
                            :items="typeOfInsurance_international"
                            item-value="value"
                            item-text="description"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Застраховка на пратката"
                            ></v-select>
                        </v-flex>
                        <v-flex xs10 sm10 md5>
                            <v-select
                            required
                            :rules='parcelValidationParcelRules'
                            v-model="priority_international"
                            :items="typeOfPriority_international"
                            item-value="value"
                            item-text="description"
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Приоритет на пратката"
                            ></v-select>
                        </v-flex>
                    </v-layout>

                    <v-slide-x-transition ease-in>
                        <v-layout v-show='!international_weightValidate' row wrap justify-center>
                            <v-flex xs8 sm10 md10>
                                <v-alert
                                type="error"
                                dismissible
                                > Надвишаваш максималното допустимо тегло за избраната услуга
                                </v-alert>
                            </v-flex>
                        </v-layout>
                    </v-slide-x-transition>

                    <v-layout row wrap align-center justify-center>
                        <v-flex xs8 sm4 md3>
                            <v-btn
                            block
                            rounded
                            color="primary"
                            type="submit"
                            :disabled='canProceedOrder || !international_weightValidate'
                        >
                            Покажи цена</v-btn
                        >
                        </v-flex>
                        <v-flex v-if='parcelPriceRecieved' justify-center xs8 sm6 md2>
                            <v-text-field
                            readonly
                            :value='parcelPrice'
                            dense
                            suffix='лв.'
                            class="text-md-h5 font-weight-bold centered-input mt-3"
                            color="primary" >
                            </v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-expand-x-transition ease-in>
                     <v-layout v-show='is_parcel_outside_schedule' row wrap justify-center>
                        <v-flex xs8 sm10 md10>
                                <v-alert
                                type="info"
                                dismissible
                                > {{ scheduled_day_delivery }}
                                </v-alert>
                        </v-flex>
                    </v-layout>
                    </v-expand-x-transition>

                    <v-expand-x-transition ease-in>
                     <v-layout v-if='parcelAlert' row wrap justify-center>
                        <v-flex xs8 sm10 md10>
                                <v-alert
                                type="error"
                                dismissible
                                > {{ parcelMessage }}
                                </v-alert>
                        </v-flex>
                    </v-layout>
                    </v-expand-x-transition>

                
                    <v-card-text color='primary'>
                        Цените са крайни, с вкл. такса за гориво, тол и включено ДДС.<br>
                        <strong>{{international_selected_service_details}}</strong>
                        <!-- <strong>Калкулира се обемно тегло на пратката в случай че надвишава реалното по формула Д*Ш*В/6000</strong><br>
                        Пример: кашон с размери 50х50х50см - обемно тегло 50*50*50=125,000 / 6000 = 20,83кг <br> -->
                    </v-card-text>
</v-form>
                    <v-layout v-if='parcelPriceRecieved' row wrap justify-center>
                        <v-flex xs8 sm10 md3>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            :disabled='canProceedOrder'
                            @click="proceedDetailsInternational">
                                Напред
                            </v-btn>
                        </v-flex>
                    </v-layout>
                    


                </v-card>

                <!-- Parcel Details -->
                <v-card v-show='parcelDetails' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                        {{parcelType}} - {{weight}} kg - Цена: {{parcelPrice}}лв.
                        </v-app-bar>
                    </v-card-title>
                    <v-form
                        v-show='selectedCategory == 0'
                        ref="form_finish"
                        @submit.prevent=""
                        @submit="finishParcelDetails"
                        v-model="valid_form_finish"
                        lazy-validation
                        autocomplete='off'
                    >
                    <!-- Sender -->
                    <v-layout row wrap justify-left>
                        <v-flex xs10 sm10 md12>
                        <span><h3>Подател</h3></span>
                        </v-flex>
                    </v-layout>
                    <v-layout row wrap justify-center>
                        <v-flex xs10 sm10 md10>
                            <v-text-field
                            v-model="shipmentDescription"
                            :disabled="disableDescription"
                            background-color='white darken-3'
                            required
                            :rules="parcelValidationDescriptionRules"
                            filled
                            label="Описание на пратката"
                            hint='Кратко описание на съдържанието'
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout row wrap justify-center>
                        <v-flex xs10 sm10 md3>
                            <v-text-field
                            v-model="senderName"
                            background-color='white darken-3'
                            required
                            :rules="parcelValidationNameRules"
                            filled
                            label="Име"
                            hint='Имена на подателя'
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs10 sm10 md2>
                            <v-text-field
                            v-model="senderPhone"
                            type='number'
                            background-color='white darken-3'
                            required
                            :rules="parcelValidationPhoneRules"
                            filled
                            label="Телефон"
                            hint='Ако номерът не започва с (+359) напишете целия телефонен номер'
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs10 sm10 md3>
                            <v-text-field
                            v-model="senderEmail"
                            background-color='white darken-3'
                            required
                            :rules="parcelValidationEmailRules"
                            filled
                            label="Ел. Поща"
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs10 sm10 md2>
                            <v-text-field
                            v-model="senderOrganization"
                            background-color='white darken-3'
                            filled
                            label="Фирма"
                            hint='В случай на физическо лице оставете празно'
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout v-if='parcelDetails && (addressOfficeService || addressService)' row wrap justify-center>
                        <v-flex xs10 sm10 md2>
                            <v-text-field
                            :value="getSenderProvince"
                            background-color='white darken-3'
                            readonly
                            filled
                            label="Област на подателя"
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs10 sm10 md2>
                            <v-text-field
                            :value="getSenderCity"
                            background-color='white darken-3'
                            readonly
                            filled
                            label="Град на подателя"
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs10 sm10 md4>
                            <v-combobox
                            required
                            v-model="senderAddress"
                            :items="getSenderAddress"
                            clearable
                            background-color='white darken-3'
                            filled
                            :menu-props="{ maxHeight: '200' }"
                            label="Адрес"
                            hint='В случай, че адресът не е вече в нашата база, ще го запаметим.'
                            persistent-hint
                            ></v-combobox>
                        </v-flex>
                        <v-flex xs10 sm10 md1>
                            <v-text-field
                            v-model="senderAddressNumber"
                            background-color='white darken-3'
                            filled
                            label="№"
                            hint='№ на ул./бул.'
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs10 sm10 md1>
                            <v-text-field
                            v-model="senderBuildingNumber"
                            background-color='white darken-3'
                            filled
                            label="Бл."
                            hint='№ на блок'
                            persistent-hint
                            ></v-text-field>
                        </v-flex>
                    </v-layout>

                    <v-layout v-if='parcelDetails && (addressOfficeService || addressService)' row wrap justify-center>
                        <v-flex xs10 sm10 md1>
                            <v-text-field
                            v-model="senderEntranceNumber"
                            background-color='white darken-3'
                            filled
                            label="Вх."
                            hint='№ на вход'
                            persistent-hint
                            ></v-text-field>
