            }
        },

        addressOfficeService() {
            if(this.serviceType=='Адрес до офис') {
                return true
            } else {
                return false
            }
        },

        addressService() {
            if(this.serviceType=='Адрес до адрес') {
                return true
            } else {
                return false
            }
        },

        evropatProvinceCitiesSender() {
            let selectedProvince = this.senderProvince
            let indexCities = []

            Object.values(this.evropatDestinations).forEach(
                val => {
                    if(val.destinationProvince === selectedProvince) {
                        indexCities.push(val.destinationName)
                    }
                });

            // console.log(indexCities)
            return indexCities
        },

        evropatProvinceCitiesRecipient() {
            let selectedProvince = this.recipientProvince
            let indexCities = []

            Object.values(this.evropatDestinations).forEach(
                val => {
                    if(val.destinationProvince === selectedProvince) {
                        indexCities.push(val.destinationName)
                    }
                });

            // console.log(indexCities)
            return indexCities
        },

        canProceedOrder() {
            return this.canProceed
        },

        remainingPickupTime() {
            let time = this.pickupTimeFrom
            let holderArray = this.pickupTime

            let indexTime = this.pickupTime.findIndex(object => {
                return object === time;
            });
            holderArray = holderArray.slice(indexTime+1)
            return holderArray
        },

        userWaybills() {
            const userParcels = this.$store.getters.parcelsUser
            return userParcels
        },

