            // console.log(holder)
            return holder
        },

        getRecipientProvince() {
            let holder = this.recipientProvince
            // console.log(holder)
            return holder
        },

        getRecipientCity() {
            let holder = this.recipientCity
            // console.log(holder)
            return holder
        },

        getSenderAddress() {
            let selectedCity = this.senderCity
            let indexAddresses = []

            let indexCity = this.evropatDestinations.findIndex(object => {
                return object.destinationName === selectedCity;
            });
            
            let cityID = this.evropatDestinations[indexCity].destinationID
            // console.log(cityID)

            Object.values(this.evropatAddresses).forEach(
                val => {
                    if(val.destinationID === cityID) {
                        // console.log(val)
                        // console.log(this.evropatAddresses[val])
                        indexAddresses.push(val.addressFull)
                    }
                });
            // console.log(indexAddresses)
            return indexAddresses
        },

        getRecipientAddress() {
            let selectedCity = this.recipientCity
            let indexAddresses = []

