            } else if (request.data.status == 'paymentComplete')  {
                this.databaseWaybills = request.data.payload.response
                this.selectedWaybill = this.databaseWaybills[0]
                this.paymentLoading = false
                this.parcelFinish = false
                this.parcelWaybill = true
                this.$vuetify.goTo('#parcelCreation')
            } else if (request.data.status == 'awaitingPayment') {
                console.log(request.data.tcf_data)
                this.parcelInvoice = request.data.tcf_data.invoice
                this.transcard_payment_encoded = request.data.tcf_data.payload.ENCODED
                this.transcard_payment_checksum = request.data.tcf_data.payload.CHECKSUM
                this.transcard_can_pay = false
            }
        },

        async checkParcelInternationalPrice() {
            let is_form_ready = this.$refs.international_form_intro.validate()
            if (is_form_ready == true) {
                let payload = {}

                // console.log('SERVICE',indexService)

                let indexSendCity = this.evropatDestinations.findIndex(object => {
                        if(object.destinationProvince == this.senderProvince && object.destinationName == this.senderCity) {
                            if(object.destinationZone == '9'){
                                this.is_parcel_outside_schedule = true
                                this.scheduled_day_delivery = "Избраното населено място е извън зоната на обслужване. Начислена е допълнителна такса за услугата."
                            }
                            return object.destinationName === this.senderCity;
                        }
                    });

                // this.destinationType = this.selectedCategory
                payload = {
                        // 'deliveryDestinationType': this.destinationType,
                        'serviceType': this.serviceType_internaltional,
                        'parcelWeight': this.international_weight,
                        'sender': {
                            'senderProvince': this.senderProvince,
                            'senderCity': this.evropatDestinations[indexSendCity].destinationID,
                        },
                        'recipient': {
                            'recipientCountry': this.recipientCountry,
                        },
                        'insurance': this.insurance_international,
                        'priority': this.priority_international
                    }
                // console.log('Payload', payload)
                let request = await axios.post('/parcel/direx/check-price', payload)
                this.parcelPrice = (parseFloat(request.data)).toFixed(2)
                this.parcelPriceRecieved = true
                // this.parcelPrice = (parseFloat(request.data)).toFixed(2)
                // this.parcelPriceRecieved = true
            
            } else {
                void(0)
            }
        },

