<script>
import axios from 'axios';

export default {
    name: 'ParcelCheckout',


    data: () => ({
        loading_data: true,
        notCompleted: false,
        payment_successful: false,
        show_payment_message: false,
        parcel_exists: false,
        payment_message: '',
        payment_message_type: '',
        payment_status: '',
        parcel_data: {},
        checkoutPage: true,
        waybillPage: false,

        loadingData: true,

        show_courier_message: false,
        courier_message: '',
        courier_message_type: '',
        transcard_payment_encoded: '',
        transcard_payment_checksum: '',

        // - Waybill
        selectedWaybill: '',
        databaseWaybills: [],
        database_data: {},
        instructionsWaybill: false,
        pickupTimeFrom: '',
        pickupTimeTo: '',
        pickupTime: ['09:00', '09:15', '09:30', '09:45', '10:00', '10:15', '10:30', '10:45', '11:00', '11:15', '11:30', '11:45',
        '12:00', '12:15', '12:30', '12:45', '13:00', '13:15', '13:30', '13:45', '14:00', '14:15', '14:30', '14:45',
        '15:00', '15:15', '15:30', '15:45', '16:00', '16:15', '16:30', '16:45', '17:00', '17:15', '17:30', '17:45'],

        
    }),

    computed: {
        transcard_url_ok() {
            return 'https://pratkabg.com/checkout?parcel_invoice=' + this.parcel_invoice  + '&phone_number=' + this.phone_number  + '&shipment_type=' + this.shipment_type
        },

        transcard_url_cancel() {
            return 'https://pratkabg.com/checkout?parcel_invoice=' + this.parcel_invoice  + '&phone_number=' + this.phone_number  + '&shipment_type=' + this.shipment_type
        },
        remainingPickupTime() {
            let time = this.pickupTimeFrom
            let holderArray = this.pickupTime

            let indexTime = this.pickupTime.findIndex(object => {
                return object === time;
            });
            holderArray = holderArray.slice(indexTime+1)
            return holderArray
        },
        parcel_invoice() {
            return this.$route.query.parcel_invoice
        },
        phone_number() {
            return this.$route.query.phone_number
        },
        shipment_type() {
            return this.$route.query.shipment_type
        },
        delivery_type() {
            try {
                let holder = this.parcel_data.parcelDetails.deliveryType
                if(holder == 1){
                    return 'Офис до офис'
                }if(holder == 2){
                    return 'Офис до адрес'
                }if(holder == 3){
                    return 'Адрес до офис'
                }else{
                    return 'Адрес до адрес'
                }
            } catch(e){
                return false
            }
        },
        priority_type() {
            try {
                let holder = this.parcel_data.priority
                if(holder == 'E'){
                    return 'Икономична'
